.loading_rotate{
  width:100px;
  height:100px;
  border:10px solid black;
  border-radius:50%;
  border-top-color:#7878ff;
  animation: rotate 2s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite;
}

@keyframes rotate {
  from {
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}
